<template>
  <div>
    <audio-player
      serverName="sed"
      :processCallback="callback"
      :onplay="onplay"
    >
      <li>
        <p>Active Events: </p>
        <p style="color: #dd4814;">{{result}}</p>
      </li>
    </audio-player>
  </div>
</template>

<script>
import AudioPlayer from "../../components/audioPlayer";

export default {
  components: {
    AudioPlayer,
  },
  data() {
    return {
      result: "",
      list: [],
    };
  },
  methods: {
    callback(res) {
      this.list = res.result;
      this.result = res.result[0][1];
    },
    onplay(nowtime) {
      var temp = parseInt(nowtime);
      if (this.list.length > temp) {
        this.result = this.list[temp][1];
      }
    },
  },
};
</script>